.profile {
  color: #8c9aa8;
  text-align: center;
  border: 1px solid rgb(232, 236, 242);
  border-radius: 2px;
  width: 398px;
  box-shadow: dimgrey;
  margin: 0 auto;
  .avatar {
    height: 159px;
    margin: 46px 0 16px;
    border-radius: 100px;
  }
}

.name {
  font-size: 28px;
  font-weight: 600;
  color: darkslategray;
}
